<template>
  <div class="role-index">
    <div class="crumbs-box ">
      <span class="crumbs">权限管理</span>
      <span class="crumbs line">/</span>
      <span class="crumbs">角色列表</span>
    </div>
    <div class="content-page">
      <div class="table-header">
        <div class="table-header-left">
          <div class="tips-tit">角色列表</div>
        </div>
        <div class="table-header-right">
          <div class="add-btn hover" @click="addLayer()">创建角色</div>
        </div>
      </div>
      <div class="admin-table" style="width: 100%">
        <el-table :data="tableData" style="width: 100%" :header-cell-style="tableHeaderStyle">
          <el-table-column prop="id" label="序号" width="80"></el-table-column>
          <el-table-column prop="name" label="角色" width="180"></el-table-column>
          <el-table-column prop="desc" label="角色描述" width="500"></el-table-column>
          <el-table-column prop="status" label="是否启用" width="">
            <template slot-scope="scope">
              <div>
                <el-switch
                  style="display: block"
                  v-model="scope.row.status==1?true:false"
                  active-color="#EE5567"
                  inactive-color="#999999"
                  @change="isStartUsing(scope)">
                </el-switch>
              </div>
            </template>
          </el-table-column>
          <el-table-column prop="create_time" label="创建时间" width=""></el-table-column>
          <el-table-column label="操作" width="150">
            <template slot-scope="scope">
              <div class="table-btn hover2" @click="editLayer(scope.row.id)">编辑</div>
              <div class="table-btn hover2" @click="detailsLayerShow(scope.row.id)">查看</div>
            </template>
          </el-table-column>
        </el-table>
        <div class="page-box" v-if="tableData.length>0">
          <el-pagination
            @current-change="handleCurrentChange"
            background
            layout="prev, pager, next"
            :total="totalPage">
          </el-pagination>
        </div>
      </div>

    </div>
    <!-- layer 添加 编辑 -->
    <div class="layer-box-page " id="addEditLayer">
      <div class="scroll-box">
        <div class="layer-content-page" style="width: 1200px;">
          <div class="layer-title-box">
            <div class="title">{{layerTitle}}</div>
            <img src="../../assets/layer_close_icon.png" class="layer-close-icon hover" @click="closeLayer">
          </div>
          <div class="layer-content box-sizing">
            <div class="content" style="margin-top: 0;">
              <div class="layer-row-item">
                <label for="" class="row-item-label">
                  <span class="require">*</span>
                  <span class="label">帐号</span>
                </label>
                <div class="row-item-input">
                  <input type="text" class="admin-input" v-model="form.name" placeholder="请为角色命名，如：店长">
                </div>
              </div>
              <div class="layer-row-item">
                <label for="" class="row-item-label">
                  <span class="require">*</span>
                  <span class="label">角色描述</span>
                </label>
                <div class="row-item-input">
                  <textarea class="admin-textarea textarea" v-model="form.desc" placeholder="请描述角色工作职责，如：店铺运营总负责人。"></textarea>
                </div>
              </div>
              <div class="layer-row-item">
                <label for="" class="row-item-label">
                  <span class="require">*</span>
                  <span class="label">是否开启</span>
                </label>
                <div class="row-item-input">
                  <div class="flex-row" style="height: 50px;">
                    <div :class="['is-switch hover', form.status==1?'active':'']" @click="selectIsSwitch(1)">
                      <img src="../../assets/ic_xuanzhong.png" class="radio-icon" v-if="form.status==1">
                      <img src="../../assets/ic_weixuanzhong.png" class="radio-icon" v-else>
                      <span>是</span>
                    </div>
                    <div :class="['is-switch hover', form.status==2?'active':'']" @click="selectIsSwitch(2)">
                      <img src="../../assets/ic_xuanzhong.png" class="radio-icon" v-if="form.status==2">
                      <img src="../../assets/ic_weixuanzhong.png" class="radio-icon" v-else>
                      <span>否</span>
                    </div>
                  </div>
                </div>
              </div>
              <div class="layer-row-item" style="margin-bottom: 10px;">
                <label for="" class="row-item-label">
                  <span class="label">菜单权限</span>
                </label>
                <div class="row-item-input">

                </div>
              </div>
              <ul class="quan-list-box">
                <li class="quan-item" v-for="(item, index) in menuList" :key="index">
                  <div class="quan-left">
                    <el-checkbox v-model="item.checkAll" @change="checkAllChange(item)">{{item.label}}</el-checkbox>
                  </div>
                  <div class="quan-right">
                    <el-checkbox-group class="checkbox-box" v-model="item.checked" @change="checkedChange(item)">
                      <el-checkbox v-for="(subItem, subIndex) in item.power" :label="subItem.id" :key="subIndex">{{subItem.name}}</el-checkbox>
                    </el-checkbox-group>
                  </div>
                </li>
              </ul>

              <div class="admin-btn-box" style="margin: 100px 0 50px;">
                <div class="btn btn2 hover" @click="onSubmitData" v-if="!form.ids">创建</div>
                <div class="btn btn2 hover" @click="onSubmitData" v-else>确定</div>
              </div>

            </div>
          </div>
        </div>
      </div>
    </div>
    <!-- layer 查看 -->
    <div class="layer-box-page " id="detailsLayer">
      <div class="scroll-box">
        <div class="layer-content-page" style="width: 800px;">
          <div class="layer-title-box">
            <div class="title">查看帐号</div>
            <img src="../../assets/layer_close_icon.png" class="layer-close-icon hover" @click="closeLayer">
          </div>
          <div class="layer-content box-sizing">
            <div class="content" style="width: 500px;margin: 0 auto;">
              <form class="form-box" v-if="dataInfo">
                <div class="form-item">
                  <label class="form-label">
                    <span class="require">*</span>
                    <span class="label">角色</span>
                  </label>
                  <div class="form-input-box">
                    <div class="details-text">{{dataInfo.name}}</div>
                  </div>
                </div>
                <div class="form-item">
                  <label class="form-label">
                    <span class="require">*</span>
                    <span class="label">角色描述</span>
                  </label>
                  <div class="form-input-box">
                    <div class="details-text">{{dataInfo.desc}}</div>
                  </div>
                </div>
                <div class="form-item">
                  <label class="form-label">
                    <span class="require">*</span>
                    <span class="label">是否开启</span>
                  </label>
                  <div class="form-input-box">
                    <div class="details-text" v-if="dataInfo.status==1">开启</div>
                    <div class="details-text" v-else>关闭</div>
                  </div>
                </div>
                <div class="form-item" style="margin-bottom: 10px;">
                  <label class="form-label">
                    <span class="label">菜单权限</span>
                  </label>
                  <div class="form-input-box">

                  </div>
                </div>
                <div class="" style="padding-left: 125px;">
                  <ul class="flex-wrap" v-for="(item, index) in dataInfo.rules" :key="index" style="margin-bottom: 10px;">
                    <li v-if="item.id==1">店铺管理：</li>
                    <li v-else-if="item.id==2">帐号管理：</li>
                    <li v-else-if="item.id==3">权限管理：</li>
                    <li class="power-css" v-for="(sub, subIndex) in item.power" :key="subIndex">
                      <span v-if="sub==1">查看</span>
                      <span v-else-if="sub==2">创建</span>
                      <span v-else-if="sub==3">编辑</span>
                      <span v-else-if="sub==4">开启</span>
                    </li>
                  </ul>
                </div>
                <div class="form-item" style="margin-top: 30px;">
                  <div class="form-box-btn">
                    <div class="form-btn hover" @click="closeLayer">返回</div>
                  </div>
                </div>
              </form>

            </div>
          </div>
        </div>
      </div>
    </div>


  </div>
</template>
<script>
  import { Rulelist, Ruleche, RuleAdd, RuleEdit } from '@/axios/api'
  import { mapState } from 'vuex'
  export default {
    name: 'role-index',
    data() {
      return {
        token: sessionStorage.getItem('token'),
        page: 1,
        totalPage: 1,
        layerTitle: '创建角色',
        tableHeaderStyle: {
          'background': '#FAFAFA',
          'height': '76px'
        },
        tableData: [],
        form: {
          ids: '',
          name: '',
          desc: '',
          rules: [],
          status: 1
        },
        // 帐号权限菜单
        menuList: [ // 数组排序跟菜单栏保持一致（包括id）
          {
            id: 1,
            label: '店铺管理',
            checkAll: false, // 是否全选
            checked: [], // 选择的id
            power: [
              {id: 1,name: '查看'},
              {id: 2,name: '创建'},
              {id: 3,name: '编辑'},
              {id: 4,name: '开启/禁用'}
            ]
          },
          {
            id: 2,
            label: '帐号管理',
            checkAll: false,
            checked: [],
            power: [
              {id: 1,name: '查看'},
              {id: 2,name: '创建'},
              {id: 3,name: '编辑'},
              {id: 4,name: '开启/禁用'}
            ]
          },
          {
            id: 3,
            label: '权限管理',
            checkAll: false,
            checked: [],
            power: [
              {id: 1,name: '查看'},
              {id: 2,name: '创建'},
              {id: 3,name: '编辑'},
              {id: 4,name: '开启/禁用'}
            ]
          }
        ],
        dataInfo: null
      }
    },
    computed: {
      ...mapState(["host"])
    },
    components: {},
    mounted() {
      this.$store.state.navActive =  this.$route.query.navActive;
      setTimeout(function(){
        $(".el-input__inner").css({
          width: '250px',
          height: '50px'
        });
        $(".form-select .el-input__inner").css({
          width: '400px',
          height: '50px'
        })
      }, 10);
      this.getData();
    },
    methods: {
      getData() {
        Rulelist({
          token: this.token,
          page: this.page
        }).then(res => {
          // console.log(res);
          if (res.status == 1) {
            let list = res.data.list;
            this.totalPage = res.data.total;
            if (list.length > 0) {
              for(let i in list) {
                list[i].create_time = this.createTime(list[i].create_time)
              }
            }
            this.tableData = list;
          } else {
            this.$message.error(res.msg)
          }
        })
      },
      // 权限菜单=====================
      checkAllChange(item) {
        let list = item.power;
        let checked = [];
        if (item.checkAll) {
          for(let i = 0 ; i < list.length ; i++) {
            checked.push(list[i].id)
          }
          item.checked = checked
        } else {
          item.checked = []
        }
      },
      checkedChange(item) {
        // console.log(item) ;// 选中的id ；arr
        item.checked.length > 0 ? item.checkAll = true : item.checkAll = false
      },
      // ==============
      isStartUsing(item) {
        // item.row.status==0
        let status = this.util.power('3', '4');
        if(status) {
          RuleEdit({
            token: this.token,
            ids: item.row.id,
            name: item.row.name,
            rules: item.row.rules,
            desc: item.row.desc,
            status: item.row.status == 1 ? 2 : 1
          }).then(res => {
            if (res.status == 1) {
              let tips = item.row.status == 1 ? '已禁用' : '已启用';
              this.$message.success(tips);
              this.getData();
            } else {
              this.$message.error(res.msg)
            }
          })
        } else {
          this.$message.error('暂无权限')
        }
      },
      addLayer() {
        let status = this.util.power('3', '2');
        if(status) {
          this.layerTitle = '创建角色';
          $("#addEditLayer").fadeIn(100);
        } else {
          this.$message.error('暂无权限')
        }
      },
      editLayer(id) {
        let status = this.util.power('3', '3');
        if(status) {
          Ruleche({
            token: this.token,
            ids: id
          }).then(res => {
            if(res.status == 1) {
              let info = res.data;
              this.form = {
                ids: id,
                name: info.name,
                desc: info.desc,
                rules: info.rules,
                status: info.status
              };
              let menuList = this.menuList;
              for(let i in menuList) {
                if(info.rules) {
                  if(info.rules.length > 0) {
                    for(let j in info.rules) {
                      if (parseInt(menuList[i].id) === parseInt(info.rules[j].id)) {
                        menuList[i].checkAll = true;
                        let arr = [];
                        for(let k in info.rules[j].power){
                          arr.push(parseInt(info.rules[j].power[k]))
                        }
                        menuList[i].checked = arr ;
                        break
                      }
                    }
                  }
                }
              }
              this.menuList = menuList;
              this.layerTitle = '编辑';
              $("#addEditLayer").fadeIn(100);
            } else {
              this.$message.error(res.msg)
            }
          });
        } else {
          this.$message.error('暂无权限')
        }
      },
      onSubmitData() {
        let formData = this.form;
        if (!formData.name) {
          this.$message.error('请输入角色名');
          return
        }
        if (!formData.desc) {
          this.$message.error('请输入角色描述');
          return
        }
        // 菜单权限
        let menuPower = this.menuList;
        let rules = [];
        for(let i in menuPower) {
          if(menuPower[i].checkAll){
            rules.push({
              id: menuPower[i].id,
              power:  menuPower[i].checked
            })
          } else {
            rules.splice(i, 1)
          }
        }
        formData.rules = rules;
        formData.token = this.token;
        if (!formData.ids) {
          RuleAdd(formData).then(res => {
            if(res.status == 1) {
              this.$message.success('添加成功');
              this.closeLayer();
              this.getData();
            } else {
              this.$message.error(res.msg)
            }
          })
        } else {
          RuleEdit(formData).then(res => {
            if(res.status == 1) {
              this.$message.success('编辑成功');
              this.closeLayer();
              this.getData();
            } else {
              this.$message.error(res.msg)
            }
          })
        }
      },
      // 关闭弹框
      closeLayer() {
        this.form = {
          ids: '',
          name: '',
          desc: '',
          rules: [],
          status: 1
        };
        let menuList = this.menuList;
        for(let i in menuList) {
          menuList[i].checkAll = false;
          menuList[i].checked = [];
        }
        $("#addEditLayer").fadeOut(100);
        $("#detailsLayer").fadeOut(100);
      },
      selectIsSwitch(num) {
        this.form.status = num;
      },
      // 详情
      detailsLayerShow(id) {
        let status = this.util.power('3', '1');
        if(status) {
          Ruleche({
            token: this.token,
            ids: id
          }).then(res => {
            if(res.status == 1) {
              this.dataInfo = res.data;
              $("#detailsLayer").fadeIn(100);
            } else {
              this.$message.error(res.msg)
            }
          });
        } else {
          this.$message.error('暂无权限')
        }
      },
      // 分页
      handleCurrentChange(curr) {
        // console.log(curr);
        this.page = curr;
        this.getData();
      },
      addTime(m){return m<10?'0'+m:m },
      createTime(value){
        let time = new Date(value*1000);
        let year = time.getFullYear();
        let month = time.getMonth()+1;
        let date = time.getDate();
        let hours = time.getHours();
        let minutes = time.getMinutes();
        let seconds = time.getSeconds();
        return year+'-'+this.addTime(month)+'-'+this.addTime(date)+' '+this.addTime(hours)+':'+this.addTime(minutes)+':'+this.addTime(seconds);
      },//2017-05-08 10:31:27
    }
  }
</script>
<style lang="less" scoped>
  .el-select-dropdown__item {
    font-size: 14px;
    padding: 0 20px;
    position: relative;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    color: #606266;
    height: 34px;
    line-height: 34px;
    box-sizing: border-box;
    cursor: pointer;
  }
  .el-select-dropdown__item.selected{
    color: #409EFF !important;
  }
  .checkbox-box .el-checkbox{
    margin-left: 30px;
  }
</style>



